import { Route, Routes, Navigate } from 'react-router-dom';
import HomePage from '../pages/HomePage';
import CityPage from '../pages/CityPage';

const AppRouter = () => {
  return (
    <Routes>
      <Route path="/pet_project_weather" exact element={<HomePage />} />
      <Route path="/pet_project_weather/city/:id" element={<CityPage />} />

      <Route path="*" element={<Navigate to="/pet_project_weather" replace />} />
    </Routes>
  );
};

export default AppRouter;
